class DashboardComponent {
    constructor(dashboardService, router) {
        this.dashboardService = dashboardService;
        this.router = router;
        this.isLoader = false;
        this.ticketCount = 0;
        this.scheduledCount = 0;
        this.intakeCount = 0;
        this.checkoutCount = 0;
        this.apppointments = [];
        this.chartType = 'line';
        //barChart
        this.barChartOptions = {
            scaleShowVerticalLines: false,
            responsive: true
        };
        this.mbarChartLabels = ['2012', '2013', '2014', '2015', '2016', '2017', '2018'];
        this.barChartType = 'bar';
        this.barChartLegend = true;
        this.barChartColors = [
            {
                backgroundColor: 'rgb(1, 192, 200)',
                borderColor: 'rgba(105,159,177,1)',
                pointBackgroundColor: 'rgba(105,159,177,1)',
                pointBorderColor: '#fafafa',
                pointHoverBackgroundColor: '#fafafa',
                pointHoverBorderColor: 'rgba(105,159,177)'
            },
            {
                backgroundColor: 'rgb(70, 159, 180)',
                borderColor: 'rgba(77,20,96,1)',
                pointBackgroundColor: 'rgba(77,20,96,1)',
                pointBorderColor: '#fff',
                pointHoverBackgroundColor: '#fff',
                pointHoverBorderColor: 'rgba(77,20,96,1)'
            }
        ];
        this.barChartData = [
            { data: [55, 60, 75, 82, 56, 62, 80], label: 'Company A' },
            { data: [58, 55, 60, 79, 66, 57, 90], label: 'Company B' }
        ];
    }
    ngOnInit() {
        this.getData();
        this.chartData = [{
                data: [3, 1, 4, 2, 3],
                label: 'Hospitals',
                fill: true
            }];
        this.chartData1 = [{
                data: [3, 1, 4, 2, 5],
                label: 'Doctors',
                fill: true
            }];
        this.chartData2 = [{
                data: [3, 5, 4, 2, 1],
                label: 'Patients',
                fill: true
            }];
        this.chartLabels = ['Jan', 'Feb', 'Mar', 'Apr', 'May'];
        this.chartColors = [{
                backgroundColor: 'rgb(250,125,180)',
                borderColor: '105,159,177,1'
            }];
        this.chartLabels = ['Jan', 'Feb', 'Mar', 'Apr', 'May'];
        this.chartColors1 = [{
                backgroundColor: 'rgb(99,216,252)',
                borderColor: '153, 250, 166,1'
            }];
        this.chartLabels = ['Jan', 'Feb', 'Mar', 'Apr', 'May'];
        this.chartColors2 = [{
                backgroundColor: 'rgb(153, 250, 166)',
                borderColor: '105,159,177,1'
            }];
        this.chartOptions = {
            scales: {
                yAxes: [{
                        ticks: {
                            beginAtZero: true,
                            stepSize: 1
                        }
                    }]
            },
            annotation: {
                drawTime: 'beforeDatasetsDraw',
                annotations: [{
                        type: 'box',
                        id: 'a-box-1',
                        yScaleID: 'y-axis-0',
                        yMin: 0,
                        yMax: 1500,
                        backgroundColor: '#4cf03b'
                    }, {
                        type: 'box',
                        id: 'a-box-2',
                        yScaleID: 'y-axis-0',
                        yMin: 1,
                        yMax: 2.7,
                        backgroundColor: '#fefe32'
                    }, {
                        type: 'box',
                        id: 'a-box-3',
                        yScaleID: 'y-axis-0',
                        yMin: 2.7,
                        yMax: 5,
                        backgroundColor: '#fe3232'
                    }]
            }
        };
    }
    getData() {
        this.isLoader = true;
        this.dashboardService.getDoctorDashboard().subscribe((res) => {
            if (res.status == '200') {
                this.ticketCount = res.data.ticket;
                this.scheduledCount = res.data.scheduled;
                this.intakeCount = res.data.intake;
                this.checkoutCount = res.data.checkout;
                this.apppointments = res.data.apppointments;
                this.isLoader = false;
            }
        });
    }
    gotoPatientProfile(id) {
        this.router.navigate([`app/patient/profile/${id}`]);
    }
    // events
    chartClicked(e) {
        // console.log(e);
    }
    chartHovered(e) {
        // console.log(e);
    }
    randomize() {
        let data = [
            Math.round(Math.random() * 100),
            Math.round(Math.random() * 100),
            Math.round(Math.random() * 100),
            (Math.random() * 100),
            Math.round(Math.random() * 100),
            (Math.random() * 100),
            Math.round(Math.random() * 100)
        ];
        let clone = JSON.parse(JSON.stringify(this.barChartData));
        clone[0].data = data;
        this.barChartData = clone;
    }
    // naviagtes to calander
    navigateToCalender() {
        this.router.navigate(['app/calender']);
    }
}


